<script>
export default {
    data: function(){
        return {
            email: 'chrigorcontato@gmail.com'
        }
    },
    methods: {
        copyInput: function(){
            navigator.clipboard.writeText(this.email).then(() => {
                const divEmail = document.querySelector('.copy-input-email');
                divEmail.classList.add('active');
                setTimeout(function(){
                    divEmail.classList.remove('active');
                    
                }, 2500);
            });
        }
    }
}
