<template>
    <div class="carousel">
        <Carousel :itemsToShow="itemsApresentaion" :wrapAround="true" :transition="500">
            <Slide v-for="slide in slides" :key="slide">
                <img :src="slide.image" alt="">
                <p id="paragraphCarrousel">{{ slide.title }}</p>
            </Slide>
            <template #addons>
                <navigation />
                <pagination />
            </template>
        </Carousel>
    </div>
</template>
  
<script>
import { Carousel, Pagination, Slide } from 'vue3-carousel'

import 'vue3-carousel/dist/carousel.css'

export default {
    data() {
        return {
            itemsApresentaion: 6,
            windowWidth: window.innerWidth,
            slides: [
                {
                    title: 'JavaScript',
                    image: require('@/assets/svg/javascript.svg')
                },
                {
                    title: 'Node.JS',
                    image: require('@/assets/svg/nodejs.svg')
                }, 
                {
                    title: 'MySQL',
                    image: require('@/assets/svg/mysql.svg')
                },
                {
                    title: 'MongoDB',
                    image: require('@/assets/svg/mongodb.svg')
                },
                {
                    title: 'Docker',
                    image: require('@/assets/svg/docker.svg')
                }, 
                {
                    title: 'Delphi',
                    image: require('@/assets/svg/delphi.svg')
                },
                {
                    title: 'TypeScript',
                    image: require('@/assets/svg/typescript.svg')
                },
                {
                    title: 'Vue.JS',
                    image: require('@/assets/svg/vuejs.svg')
                } 
            ]
        }
    },
    components: {
        Carousel,
        Slide,
        Pagination
    },
    mounted() {
        window.addEventListener('resize', this.handleResize);
    },
    beforeUnmount() {
        window.removeEventListener('resize', this.handleResize);
    },
    methods: {
        handleResize() {
            this.windowWidth = window.innerWidth;
            const size = this.windowWidth;

            this.modifyItemsCarrousel(size);
        },
        modifyItemsCarrousel(WindowSize) {
            let items = 6;

            if (WindowSize <= 1000) {
                items = 4;
            }

            this.itemsApresentaion = items;
        }
    }
}
</script>

<style scoped>

.carousel{
    display: flex;
    flex-direction: column;
}

#paragraphCarrousel{
    font-size: 16px;
}

p{
    margin: 0;
}

img{
    width: 80%;
    height: 80%;
}

.carousel__viewport {
    perspective: 1px;
}

.carousel__track {
    transform-style: preserve-3d;
}

.carousel__slide--sliding {
    transition: 0.5s;
}

.carousel__slide {
    opacity: 0.3;
    transform: rotateY(-20deg) scale(0.9);
    font-family: 'Noto', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.carousel__slide--active ~ .carousel__slide {
    transform: rotateY(20deg) scale(0.9);
}

.carousel__slide--prev {
    opacity: 0.9;
    transform: rotateY(-10deg) scale(0.95);
}

.carousel__slide--next {
    opacity: 0.9;
    transform: rotateY(10deg) scale(0.95);
}

.carousel__slide--active {
    opacity: 1;
    transform: rotateY(0) scale(1.1);
}

@media only screen and  (max-width: 600px) {

    #paragraphCarrousel{
        font-size: 10px;
    }
}

</style>
  